import Bscroll from "better-scroll";
export default {
  data() {
    return {
      playerList: []
    };
  },
  methods: {
    handleScores() {},
    handleApplyCard(item) {
      console.log(item,'oo')
      sessionStorage.setItem('singleApply',JSON.stringify(item))
      this.$router.push({name: "certificatePerson"});
    }
  },
  created() {
    let examPlanId = this.$route.query.examPlanId;
    let orderNo = this.$route.query.orderNo;
    let params = {
      examPlanId,
      orderNo
    };
    this.$axios.get("/api/enroll/grade_list", { params }).then(res => {
      if (res.data.code === 0) {
        this.playerList = res.data.data;
        this.$nextTick(() => {
          this.scroll = new Bscroll(this.$refs.wrapper, {});
        });
      }
    });
  }
};



